<template>
  <y-batch-import
    v-bind="$attrs"
    :columns="columns"
    v-on="$listeners"
  ></y-batch-import>
</template>

<script>
import { priceUnitOptions } from './unit'
export default {
  name: 'YSkuImport',
  data() {
    return {
      columns: [
        {
          label: '',
          prop: 'name',
          type: 'input',
          required: true
        },
        {
          label: '',
          prop: 'brand',
          type: 'input'
        },
        {
          label: '',
          prop: 'spec',
          type: 'input',
          required: true
        },
        {
          label: '',
          prop: 'oneCategory',
          type: 'input',
          required: true
        },
        {
          label: '',
          prop: 'twoCategory',
          type: 'input',
          required: true
        },
        {
          label: '',
          prop: 'threeCategory',
          type: 'input',
          required: true
        },
        {
          label: '',
          prop: 'manufacturer',
          type: 'input'
        },
        {
          label: '',
          prop: 'shelfLifeDays',
          type: 'input',
          required: true
        },
        {
          label: '',
          prop: 'priceUnit',
          type: 'select',
          options: priceUnitOptions,
          required: true
        },
        {
          label: ',',
          prop: 'normal',
          type: 'select',
          options: [{ label: '', value: '' },
            { label: '', value: '' }],
          required: true
        },
        {
          label: '(%)',
          prop: 'salesTaxRate',
          type: 'input-number',
          required: true
        },
        {
          label: '(%)',
          prop: 'procurementTaxRate',
          type: 'input-number',
          required: true
        },
        {
          label: '',
          prop: 'saleType',
          type: 'select',
          options: [
            // { label: '-', value: '-' },
            { label: '-', value: '-' },
            { label: '', value: '' },
            { label: '', value: '' }
          ],
          required: true
        },
        {
          label: '',
          prop: 'temperature',
          type: 'select',
          options: [{ label: '-18', value: '-18' },
            { label: '08', value: '08' },
            { label: '', value: '' }],
          required: true
        }
      ]
    }
  }
}
</script>

