<template>
  <div class="batch-import">
    <!--  -->
    <el-button
      type="primary"
      icon="el-icon-upload"
      @click="dialogVisible=true"
    >{{btnText}}</el-button>
    <!--  end -->

    <!--   -->
    <el-dialog
      :title="btnText"
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
      modal-append-to-body
      lock-scroll
      destroy-on-close
      width="92%"
      :before-close="handleBeforeClose"
    >
      <el-upload
        class="upload"
        action=""
        :multiple="false"
        :show-file-list="false"
        accept="csv, application/vnd.ms-excel, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
        :http-request="httpRequest"
      >
        <el-button
          type="primary"
          icon="el-icon-upload"
        ></el-button>
        <div
          class="el-upload__tip mt-10"
          slot="tip"
        >
          excel<span v-if="size">{{size}}kb</span>
          <el-link
            class="template"
            type="primary"
            :href="downloadUrl"
          ></el-link>
        </div>
      </el-upload>
      <el-button-group>
        <el-button
          class="mt-10"
          v-if="tableData.length"
          @click="handleToggleEdit"
        >{{!isEdit ?'' : ''}}</el-button>
      </el-button-group>

      <y-table
        :max-height="312"
        :data="tableData"
        :columns="currentColumns"
        pagination
        :total="total"
        :reload="reloadData"
        :colIndex="1"
      ></y-table>

      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="handleCancel"> </el-button>
        <el-button
          type="primary"
          @click="handleConfirm"
        > </el-button>
      </span>
    </el-dialog>
    <!--  end -->
  </div>
</template>

<script>
import XLSX from 'xlsx'
import { merge, find, isEmpty } from 'lodash'
import moment from 'moment'
// import Vue from 'vue'
export default {
  name: 'YBatchImport',
  data() {
    return {
      dialogVisible: false,
      tableData: [],
      total: 0,
      queryParams: {
        current: 1,
        size: 10
      },
      dbData: [],
      currentColumns: [],
      isEdit: false
    }
  },
  watch: {
    uploadSuccess: {
      handler(val) {
        if (val) {
          this.dialogVisible = false
          this.tableData = []
          this.dbData = []
          this.total = 0
          this.isEdit = false
          this.$forceUpdate()
        }
      },
      deep: true,
      immediate: true
    }
  },
  props: {
    btnText: {
      type: String,
      default: ''
    },
    size: {
      type: [String, Number],
      default: 0
    },
    downloadUrl: {
      type: String
      // required: true
    },
    // 
    uploadSuccess: {
      type: Boolean,
      default: false
    },
    columns: {
      type: Array,
      default() {
        return [
          {
            label: '',
            prop: 'name',
            type: 'input'
          },
          {
            label: '',
            prop: 'brand',
            type: 'input'
          },
          {
            label: '',
            prop: 'spec',
            type: 'input'
          },
          {
            label: '',
            prop: 'oneCategory',
            type: 'input'
          },
          {
            label: '',
            prop: 'twoCategory',
            type: 'input'
          },
          {
            label: '',
            prop: 'threeCategory',
            type: 'input'
          },
          {
            label: '',
            prop: 'manufacturer',
            type: 'input'
          },
          {
            label: '',
            prop: 'shelfLifeDays',
            type: 'input'
          },
          {
            label: '',
            prop: 'priceUnit',
            type: 'input'
          },
          {
            label: ',',
            prop: 'normal',
            type: 'input'
          },
          {
            label: '(%)',
            prop: 'salesTaxRate',
            type: 'input'
          },
          {
            label: '',
            prop: 'saleType',
            type: 'input'
          },
          {
            label: '(%)',
            prop: 'procurementTaxRate',
            type: 'input'
          },
          {
            label: '',
            prop: 'temperature',
            type: 'select',
            options: [{ label: '-18C', value: 'FREEZE' },
              { label: '0C8C', value: 'COLD' },
              { label: '', value: 'NORMAL' }]
          }
        ]
      }
    }
  },
  created() {
    this.currentColumns = this.columns.map((item) => {
      return {
        label: item.label,
        prop: item.prop,
        render: (h, { row }) => {
          if (this.isEdit) {
            if (item.type === 'input') {
              return <el-input v-model={this.tableData[row.index][item.prop]} size='small' clearable rules={row.rules}></el-input>
            } else if (item.type === 'select') {
              return <el-select v-model={this.tableData[row.index][item.prop]} size='small' clearable rules={row.rules}>
                {item.options.map((option) => {
                  return <el-option key={option.value}
                    label={option.label}
                    value={option.value}>
                  </el-option>
                })}
              </el-select>
            } else if (item.type === 'date-picker') {
              return <el-date-picker
                style={{ width: '95%', display: 'block' }}
                v-model={this.tableData[row.index][item.prop]}
                type='date'
                size='small'
                onChange={() => { this.tableData[row.index][item.prop] = moment(this.tableData[row.index][item.prop]).format('YYYY-MM-DD') }}
                placeholder=''>
              </el-date-picker>
            } else if (item.type === 'input-number') {
              return <y-input v-model={this.tableData[row.index][item.prop]} size='small' clearable number rules={row.rules}></y-input>
            }
          } else {
            return <div onClick={this.handleToggleEdit}>{row[item.prop]}</div>
          }
        }
      }
    })
  },
  methods: {
    // 
    validate(data) {
      return new Promise((resolve, reject) => {
        data.forEach((item, index) => {
          Object.keys(item).forEach(key => {
            // keycolumn
            const column = find(this.columns, (col) => {
              return col.prop === key
            })
            if (!isEmpty(column)) {
              // column
              if (column.required) {
                // required
                if (item[key] === '' || item[key] === undefined || item[key] === null) {
                  // 
                  reject(`${index + 1}[${column.label}] `)
                }
              }
              if (column.pattern) {
                if (!new RegExp(column.pattern).test(item[key])) {
                  reject(`${index + 1}[${column.label}] `)
                }
              }
            }
          })
        })
        resolve(true)
      })
    },
    // 
    handleBeforeClose(done) {
      this.handleCancel()
      done()
    },
    // 
    handleCancel() {
      this.tableData = []
      this.total = 0
      document.getElementsByName('file')[0].value = ''
      this.dialogVisible = false
    },
    // 
    handleConfirm() {
      this.mergeTable()
      this.validate(this.dbData).then(valid => {
        if (valid) {
          this.$emit('upload', this.dbData)
        }
      }).catch(err => {
        this.$message.warning(err)
      })
    },
    // 
    handleToggleEdit() {
      this.isEdit = !this.isEdit
      // tableDatadbData
      if (!this.isEdit) {
        this.mergeTable()
      }
    },
    // tableDatadbData
    mergeTable() {
      const { size, current } = this.queryParams
      const mergeData = this.tableData.map(item => {
        return {
          ...item,
          index: undefined
        }
      })
      this.dbData.splice((current - 1) * size, size, ...mergeData)
    },
    // 
    loadData() {
      const { size, current } = this.queryParams
      this.tableData = this.dbData.slice((current - 1) * size, current * size).map((item, index) => {
        item.index = index
        return item
      })
    },
    // 
    reloadData({ type, pageSize: size, currentPage }) {
      // 
      this.mergeTable()
      if (type === 'size-change') {
        // current1
        this.queryParams = merge(this.queryParams, { size, current: 1 })
      } else {
        // 
        this.queryParams = merge(this.queryParams, { current: currentPage })
      }
      this.loadData()
    },
    // dbData excel
    formatDbData(dbData) {
      if (!Array.isArray(dbData)) {
        throw new TypeError('Array')
      }
      const res = dbData.map(item => {
        const obj = {}
        Object.keys(item).forEach(key => {
          // keycolumn
          const column = find(this.columns, col => {
            return col.label.trim().includes(key)
          })
          if (column) {
            obj[column.prop] = item[key]
          }
        })
        return obj
      })
      return res
    },
    // 
    httpRequest(e) {
      this.tableData = []
      const file = e.file // 
      if (!file) {
        // 
        return false
      } else if (!/\.(xls|xlsx)$/.test(file.name.toLowerCase())) {
        // 
        this.$message.error('xlsxlsx')
        return false
      }

      const fileReader = new FileReader()
      fileReader.onload = (ev) => {
        try {
          const data = ev.target.result
          const workbook = XLSX.read(data, {
            type: 'binary' // 
          })
          const exlname = this.sheetName || workbook.SheetNames[0] // 
          const exl = XLSX.utils.sheet_to_json(workbook.Sheets[exlname]) // json
          //  JSON  data 
          this.dbData = this.formatDbData(exl)
          this.loadData()
          this.total = this.dbData.length
          // document.getElementsByName('file')[0].value = '' // value
        } catch (e) {
          console.log('')
          return false
        }
      }
      fileReader.readAsBinaryString(file)
    }
  }
}
</script>
<style lang="scss" scoped>
.batch-import {
  display: inline-block;
  margin-left: 10px;
}
.mt-10 {
  margin-top: 10px;
}
.template {
  position: relative;
  margin-left: 10px;
  top: -1px;
}
</style>

