<template>
  <el-input
    v-model="currentValue"
    @input="handleInputEvent"
    @change="handleInputChange"
    :clearable="$attrs.clearable || true"
    v-bind="$attrs"
    v-on="$listeners"
  ></el-input>
</template>
<script>
export default {
  name: 'YInput',
  props: {
    value: {
      type: [String, Number]
    },
    number: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    currentValue: {
      get() {
        return this.value
      },
      set(val) {
        this.handleInputValue(val)
      }
    }
  },
  methods: {
    handleInputEvent(val) {
      const reg = /^(([1-9]{1}\d{0,9})|(0{1}))\.?(\d{1,2})?$/
      this.handleInputValue(val, reg)
    },
    handleInputValue(val, reg) {
      if (this.number) {
        // number
        reg = new RegExp(reg)
        if (reg.test(val)) {
          this.$nextTick(() => {
            this.$emit('input', val)
          })
        } else {
          this.$nextTick(() => {
            this.$emit('input', val.toString().slice(0, -1))
          })
        }
      } else {
        // 
        this.$emit('input', val)
      }
    },
    handleInputChange(val) {
      const reg = /^(([1-9]{1}\d{0,9})|(0{1}))(\.\d{1,2})?$/
      this.handleInputValue(val, reg)
    }
  }
}
</script>

